import ZeroOneGame from '../../ZeroOneGameView/ZeroOneGame'

export default class CoOpZeroOneGame extends ZeroOneGame {
  constructor(players, options = { score: 1001, totalRounds: 40 }) {
    options.rounds = Math.floor(options.totalRounds / players.length)
    super(players, options)
  }

  get winners() {
    return [this.state.currentPlayer]
  }

  computePlayersGameScores = score => {
    const {
      currentPlayerGameScore,
      currentPlayerIndex,
      setPlayerGameScore,
    } = this.state
    // WORKAROUND: update the current player's game score in the last round of the loop to
    // sync the states of the ClassicDartGameView component and game state.
    for (let i = this.players.length - 1; i >= 0; i--) {
      const nextPlayerIndex = (currentPlayerIndex + i) % this.players.length
      setPlayerGameScore(nextPlayerIndex, currentPlayerGameScore - score)
    }
  }

  rollbackPlayersGameScores = round => {
    const {
      currentPlayerGameScore,
      currentPlayerIndex,
      setPlayerGameScore,
    } = this.state
    for (let i = 0; i < this.players.length; i++) {
      const nextPlayerIndex = (currentPlayerIndex + i) % this.players.length
      setPlayerGameScore(
        nextPlayerIndex,
        currentPlayerGameScore + round.scores.map(s => s.value).reduce((a, b) => a + b)
      )
    }
  }
}
