import { useContext, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { withRouter } from "react-router"
import { makeStyles } from '@material-ui/core/styles'
import { Emitter, SoundEffectPlayer } from 'shared/services'
import { Typography } from 'components'
import { CreditContext } from 'components/contexts'
import styles from './styles'

const useStyles = makeStyles(styles)

export function _Credit(props) {
  const { appState, location } = props
  const { credit, increaseCredit, decreaseCredit } = useContext(CreditContext)
  const { t } = useTranslation()
  const classes = useStyles({ hidden: location.pathname === '/game' })
  const handleCoinInsert = () => {
    increaseCredit(1)
    SoundEffectPlayer.play('coin')
  }
  const handleGameOver = (gameName, winners) => {
    const { totalCredits, credit } = appState.game
    const usedCredits = totalCredits ? totalCredits : credit * appState.players.length
    decreaseCredit(usedCredits)
  }
  useEffect(() => {
    Emitter.on('COIN_INSERT', handleCoinInsert)
    Emitter.on('GAME_OVER', handleGameOver)
    return () => {
      Emitter.off('COIN_INSERT', handleCoinInsert)
      Emitter.off('GAME_OVER', handleGameOver)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
  return (
    <Typography {...props} className={classes.root} variant='h4'>{t('credit', { credit })}</Typography>
  )
}

export const Credit = withRouter(_Credit)
