import { useContext, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import config from 'config'
import { BackgroundImageContext, CreditContext } from 'components/contexts'
import {
  CountUpGameView, NinjaCountUpGameView, CoOpZeroOneGameView, ThreeDartsOnePrizeGameView, ZeroOneGameView
} from 'components/games'

export default function GameView({ game = {}, players =[] }) {
  const history = useHistory()
  const { credit } = useContext(CreditContext)
  const { setRandomBackgroundImage } = useContext(BackgroundImageContext)
  useEffect(() => {
    game.name !== 'ninja-count-up' && setRandomBackgroundImage()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
  if (config.isArcadeMode() && credit < game.credit) {
    history.push('/')
    return null
  }
  switch(game.name) {
    case 'zero-one':
      return <ZeroOneGameView players={players} score={game.score} rounds={game.rounds} />
    case 'co-op-zero-one':
      return <CoOpZeroOneGameView players={players} score={game.score} totalRounds={game.totalRounds} />
    case 'count-up':
      return <CountUpGameView players={players} rounds={game.rounds} />
    case 'ninja-count-up':
      return <NinjaCountUpGameView players={players} rounds={game.rounds} />
    case '3-darts-1-prize':
      return <ThreeDartsOnePrizeGameView players={players} rounds={game.rounds} />
    default:
      history.push('/')
      return null
  }
}
